<template>
    <div class="popup-wrap">
        <div id="popup" class="popup item-pop" v-if="isNeedPop()">
            <div class="popup-content">
                <span class="close-btn">&times;</span>
                <h2 v-if="!popType.data">追加</h2>
                <h2 v-else>編集</h2>
                <MerchPop ref="popView" :item="popType.data" @closePop="closePopup" v-if="popType.name === 'items'"/>
                <TablePop ref="popView" :item="popType.data" @closePop="closePopup" v-else-if="popType.name === 'table'"/>
                <CatPop ref="popView" :item="popType.data" @closePop="closePopup" v-else-if="popType.name === 'category'"/>
            </div>
        </div>
    </div>
</template>
<script>
import TablePop from './TablePop.vue';
import MerchPop from './MerchPop.vue';
import CatPop from './CatPop.vue';
export default {
    name: 'PopupField',
    components: {
        TablePop, MerchPop, CatPop
    },
    props: {
        popType: Object
    },
    methods: {
        showPopup() {
            const pop = this.$el.querySelector('.popup');
            // console.log(pop);
            // console.log(this.$el.querySelector('.popup'));
            // setTimeout(() => {
            if(this.popType.data) {
                this.$refs.popView.setData(this.popType.data);
            } else {
                this.$refs.popView.setData(null);
            }
            pop.style.display = 'block';
            this.$el.querySelector('.close-btn').addEventListener('click', () => {
                pop.style.display = '';
            });
            // console.log(this.popType);
            // }, 1000);
        },

        isNeedPop() {
            const check = ['table', 'items', 'category'];
            return check.some(v => v === this.popType.name);
        },

        closePopup() {
            this.$emit('getData');
            this.$el.querySelector('.popup').style.display = '';
        }
    },
    emits: [
        'getData'
    ]
}
</script>
