<template>
  <div class="register-wrap">
    <SidebarField/>
    <div class="view-wrap">
      <RouterView ref="displays" @set-display="setDisplay" @set-data="setData" @set-method="setMethod" @open-pop="openPop"/>
    </div>

    <PopupField ref="popup" :popType="display" @getData="getData"/>
  </div>
</template>

<script>
import SidebarField from './components/SidebarField.vue';
import PopupField from './components/PopupField.vue';

export default {
  name: 'RegiMaster',
  components: {
    SidebarField,
    PopupField
  },
  data() {
    return {
      display: {
        name: 'regi',
        data: null,
        method: 'GET'
      }
    }
  },
  methods: {
    /**
     * displayのnameを変更する
     * emit用
     * @param {string} disp 
     */
    setDisplay(disp) {
      this.display.name = disp;
    },
    
    /**
     * displayのdataを変更する
     * emit用
     * @param {*} data 
     */
    setData(data) {
      this.display.data = data;
    },

    setMethod(m) {
      this.display.method = m;
    },

    /**
     * popupを表示する
     * emit用
     */
    openPop() {
        this.$refs.popup.showPopup();
    },

    openPopWithData(data) {
      this.setData(data);
      this.openPop();
    },

    getData() {
      switch(this.display.name) {
        case 'items':
          this.$refs.displays.getItems();
          break;
        case 'category':
          this.$refs.displays.getCategorys();
          break;
        case 'table':
          this.$refs.displays.getTables();
          break;
        default:
          break;
      }
    }
  }
}
</script>
