<template>
    <div class="form-wrap">
        <form id="editForm" v-if="itemData" @submit.prevent="updateData">
            <!-- テーブル編集 -->
            <label for="tableName">テーブル名</label>
            <input type="text" id="tableName" name="tableName" v-model="itemData.name" required>
            
            <label for="low-capacity">最低収容人数</label>
            <input type="number" name="lowcapacity" id="low-capacity" v-model="itemData.low_capacity">
            
            <label for="capacity">最大収容人数</label>
            <input type="number" name="capacity" id="capacity" v-model="itemData.capacity">
            
            <div class="form-row">
                <div>
                    <label for="smoking">禁煙・喫煙</label>
                    <select id="smoking" name="smoking" v-model="itemData.smoking">
                        <option value="禁煙">禁煙</option>
                        <option value="喫煙">喫煙</option>
                    </select>
                </div>
                <div>
                    <label for="type">席タイプ</label>
                    <select id="type" name="type" v-model="itemData.type">
                        <option value="テーブル">テーブル</option>
                        <option value="個室">個室</option>
                        <option value="カウンター">カウンター</option>
                    </select>
                </div>
            </div>
            
            <p>表示</p>
            <button v-if="itemData.display" type="button" id="display-toggle" class="display-toggle active" @click="btnToggle(itemData)">ON</button>
            <button v-else type="button" id="display-toggle" class="display-toggle" @click="btnToggle(itemData)">OFF</button>
            
            <button type="submit" class="save-button">保存</button>
            <!-- /テーブル編集 -->
        </form>
        <form id="editForm" v-else @submit.prevent="sendData">
            <!-- テーブル登録 -->
            <label for="tableName">テーブル名</label>
            <input type="text" id="tableName" name="tableName" v-model="tableData.name" required>
            
            <label for="low-capacity">最低収容人数</label>
            <input type="number" name="lowcapacity" id="low-capacity" v-model="tableData.low_capacity" min="1">
            <label for="capacity">最大収容人数</label>
            <input type="number" name="capacity" id="capacity" :min="tableData.low_capacity" v-model="tableData.capacity"/>
            
            <div class="form-row">
                <div>
                    <label for="smoking">禁煙・喫煙</label>
                    <select id="smoking" name="smoking" v-model="tableData.smoking">
                        <option value="禁煙">禁煙</option>
                        <option value="喫煙">喫煙</option>
                    </select>
                </div>
                <div>
                    <label for="type">席タイプ</label>
                    <select id="type" name="type" v-model="tableData.type">
                        <option value="テーブル">テーブル</option>
                        <option value="個室">個室</option>
                        <option value="カウンター">カウンター</option>
                    </select>
                </div>
            </div>
            
            <p>表示</p>
            <button v-if="tableData.display" type="button" id="display-toggle" class="display-toggle active" @click="btnToggle(tableData)">ON</button>
            <button v-else type="button" id="display-toggle" class="display-toggle" @click="btnToggle(tableData)">OFF</button>
            
            <button type="submit" class="save-button">保存</button>
            <!-- /テーブル登録 -->
        </form>
    </div>
</template>

<script>
import Ajax from '@/lib/Ajax';

export default {
    name: 'TablePop',
    data() {
        return {
            itemData: {},
            displayBool: false,
            tempData: {},
            tableData: {}
        }
    },
    mounted() {
        this.tableData.name = '';
        this.tableData.low_capacity = 1;
        this.tableData.capacity = 4;
        this.tableData.smoking = '禁煙';
        this.tableData.type = 'テーブル';
        this.tableData.display = false;
    },
    methods: {
        setData(dat) {
            if(dat) {
                this.itemData = Object.assign({}, this.itemData, dat);
            } else {
                this.itemData = null;
            }
        },
        
        /**
        * 表示するかしないかを切り替える処理
        * 多分後で変える
        * PUT
        * @param {*} dat
        */
        btnToggle(dat) {
            if(dat.display) {
                dat.display = false;
            } else {
                dat.display = true;
            }
        },

        sendData() {
            Ajax('/api/master/tables', 'POST', this.tableData);
            this.closePopup();
        },

        updateData() {
            Ajax('/api/master/tables', 'PUT', this.itemData);
            this.closePopup();
        },

        closePopup() {
            this.$emit('closePop');
        }
    },
    emits: [
        'closePop'
    ]
}
</script>
