<template>
    <div class="form-wrap">
        <form id="editForm" v-if="itemData" @submit.prevent="updateData">
            <!-- 商品変更 -->
            <label for="itemName">商品名</label>
            <input type="text" id="itemName" name="itemName" v-model="itemData.itemName" required>
            
            <label for="category">カテゴリ</label>
            <select id="category" name="category" v-model="itemData.category" required>
                <option :value="category.id" v-for="(category, value) in categorys" :key="value">{{ category.name }}</option>
            </select>
            
            <label for="price">価格</label>
            <input type="number" id="price" name="price" v-model="itemData.price" required>
            
            <label for="itemImage">商品画像</label>
            <input type="file" id="itemImage" name="itemImage" accept="image/*">
            
            <p>表示</p>
            <button v-if="itemData.display" type="button" id="display-toggle" class="display-toggle active" @click="btnToggle(itemData)">ON</button>
            <button v-else type="button" id="display-toggle" class="display-toggle" @click="btnToggle(itemData)">OFF</button>
            <!-- <div class="display-toggle-container">
                <button v-else type="button" id="display-toggle" class="display-toggle" @click="btnToggle(itemData)">ON</button>
                
                <button v-else type="button" id="display-toggle" class="display-toggle" @click="btnToggle(itemData)">OFF</button>
            </div> -->
            
            <button type="submit" class="save-button">保存</button>
            <p class="error-msg"></p>
            <!-- /商品変更 -->
        </form>

        <form id="editForm" v-else @submit.prevent="sendData">
            <!-- 商品登録 -->
            <label for="itemName">商品名</label>
            <input type="text" id="itemName" name="itemName" v-model="merchData.name" required>
            
            <label for="category">カテゴリ</label>
            <select id="category" name="category" v-model="merchData.category" required>
                <option :value="category.id" v-for="(category, value) in categorys" :key="value">{{ category.name }}</option>
            </select>
            
            <label for="price">価格</label>
            <input type="number" id="price" name="price" v-model="merchData.price" required>
            
            <label for="itemImage">商品画像</label>
            <input type="file" id="itemImage" name="itemImage" accept="image/*">

            <p>表示</p>
            <button v-if="merchData.display" type="button" id="display-toggle" class="display-toggle active" @click="btnToggle(merchData)">ON</button>
            <button v-else type="button" id="display-toggle" class="display-toggle" @click="btnToggle(merchData)">OFF</button>
            
            <button type="submit" class="save-button">保存</button>
            <p class="error-msg"></p>
            <!-- /商品登録 -->
        </form>
    </div>
</template>

<script>
import Ajax from '@/lib/Ajax';

export default {
    name: 'MerchPop',
    data() {
        return {
            itemData: {},
            displayBool: false,
            tempData: {},
            categorys: [],
            merchData: {}
        }
    },
    props: {
        item: Object
    },
    mounted() {
        this.merchData.name = '';
        this.merchData.category = null;
        this.merchData.price = 0;
        this.merchData.display = false;
        this.merchData.image = 'assets/img/noimage.png';
        this.$el.querySelector('#itemImage').value = null;
        Ajax('/api/master/categorys')
        .then(res => res.json())
        .then(dat => {
            this.categorys = dat;
        });
    },
    methods: {
        setData(dat) {
            if(dat) {
                this.itemData = Object.assign({}, this.itemData, dat);
            } else {
                this.itemData = null;
            }
        },

        btnToggle(dat) {
            if(dat.display) {
                dat.display = false;
            } else {
                dat.display = true;
            }
        },

        sendData() {
            const itemImage = document.querySelector('#itemImage');

            const file = itemImage.files[0];
            if(file) {
                this.merchData.image = `upload/img/${file.name}`;
                const form = new FormData();
                form.append('images', file, file.name);
                const opt = {
                    method: 'POST',
                    body: form
                };

                // TODO: /api/master/merchImage に変更する
                fetch('/api/master/upload/merchImage', opt)
                .then(res => {
                    if(res.status === 413) {
                        const error = document.querySelector('.error-msg');
                        error.textContent = '画像ファイルの容量が大きすぎます';
                    }
                });
            }
            
            Ajax('/api/master/insertMerch', 'POST', this.merchData);
            this.closePopup();
        },

        updateData() {
            // const name = document.querySelector('#itemName');
            // const category = document.querySelector('#category');
            // const price = document.querySelector('#price');
            const itemImage = document.querySelector('#itemImage');
            const sendData = {
                name: this.itemData.itemName,
                category: this.itemData.itemImage,
                price: this.itemData.price,
                display: this.itemData.display,
                image: this.itemData.itemImage
            };

            const file = itemImage.files[0];
            if(file) {
                sendData.image = 'upload/img/' + file.name;
                const form = new FormData();
                form.append('images', file);

                const opt = {
                    method: 'PUT',
                    body: form
                };

                // TODO: /api/master/merchImage に変更する
                fetch('/api/master/update/merchImage', opt)
                .then(res => {
                    if(res.status === 413) {
                        const error = document.querySelector('.error-msg');
                        error.textContent = '画像ファイルの容量が大きすぎます';
                    }
                });
            }

            Ajax('/api/master/insertMerch', 'PUT', sendData);
            this.closePopup();
        },

        closePopup() {
            this.$emit('closePop');
        }
    },
    emits: [
        'closePop'
    ]
}
</script>
