<template>
    <div class="form-wrap">
        <form id="editForm" v-if="itemData" @submit.prevent="updateData">
            <label for="categoryName">カテゴリー名</label>
            <input type="text" id="categoryName" name="categoryName" :value="itemData.name" required><br>
            
            <p>色</p>
            <input type="color" name="categoryColor" id="categoryColor" :value="itemData.color" required>
            
            <p>表示</p>
            <button v-if="catData.display" type="button" id="display-toggle" class="display-toggle active" @click="btnToggle(itemData)">ON</button>
            <button v-else type="button" id="display-toggle" class="display-toggle" @click="btnToggle(itemData)">OFF</button>
            
            <button type="submit" class="save-button">保存</button>
        </form>
        <form id="editForm" v-else @submit.prevent="sendData">
            <label for="categoryName">カテゴリー名</label>
            <input type="text" id="categoryName" name="categoryName" v-model="catData.name" required><br>
            
            <label for="categoryColor">色</label>
            <input type="color" name="categoryColor" id="categoryColor" v-model="catData.color" required>
            
            <p>表示</p>
            <button v-if="catData.display" type="button" id="display-toggle" class="display-toggle active" @click="btnToggle(catData)">ON</button>
            <button v-else type="button" id="display-toggle" class="display-toggle" @click="btnToggle(catData)">OFF</button>
            
            <button type="submit" class="save-button">登録</button>
        </form>
    </div>
</template>

<script>
import Ajax from '@/lib/Ajax';

export default {
    name: 'CatPop',
    data() {
        return {
            itemData: {},
            displayBool: false,
            tempData: {},
            categorys: [],
            catData: {}
        }
    },
    props: {
        item: Object
    },
    mounted() {
        this.catData.name = '';
        this.catData.color = '#ffffff';
        this.catData.display = false;
    },
    methods: {
        setData(dat) {
            if(dat) {
                this.itemData = Object.assign({}, this.itemData, dat);
            } else {
                this.itemData = null;
            }
        },

        btnToggle(dat) {
            if(dat.display) {
                dat.display = false;
            } else {
                dat.display = true;
            }
        },

        sendData() {
            Ajax('/api/master/category', 'POST', this.catData);
            this.closePopup();
        },

        updateData() {
            Ajax('/api/master/category', 'PUT', this.itemData);
            this.closePopup();
        },

        closePopup() {
            this.$emit('closePop');
        }
    },
    emits: [
        'closePop'
    ]
}
</script>
